@media screen and (max-width: 760px) {
    html,
    body,
    #root {
        overflow: auto;
    }

    aside {
        position: fixed;
        left: -100%;
        top: 0;
        z-index: 1;
        width: 40%;
        height: 100%;
    }

    header {
        width: 100%;
        padding: 6px 2%;
    }

    main {
        display: block;
        width: 100%;
    }

    .cards-wrapper {
        width: 100%;
        height: initial;
    }

    .column-wrapper {
        width: 100%;
        height: initial;
    }

    .menu-icon {
        display: initial;
    }

    .note {
        justify-content: space-evenly;
        &__single {
            width: calc(100% / 1.9);
            margin: 0 5 10px 5;
        }
    }
}

@media screen and (max-width: 425px) {
    .note {
        justify-content: center;

        &__single {
            max-width: none;
            width: 100%;
        }
    }
}
