@import 'scss/base/clearfix';

main{
    @extend .clearfix;
	width: 70%;
	min-width: calc(100% - 200px);
	height:calc(100% - 62px);
    float: right;
}
main > div {
    width: 100%;
    height: 100%;
}

main .main-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
}
